import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-apteka',
  templateUrl: './apteka.page.html',
  styleUrls: ['./apteka.page.scss'],
})
export class AptekaPage implements OnInit {

  constructor() { }

  ngOnInit() {
  }

  getBackButtonText() {
    const win = window as any;
    const mode = win && win.Ionic && win.Ionic.mode;
    return mode === 'ios' ? 'Назад' : '';
  }

}
